import Vue from 'vue'
import VueRouter from 'vue-router'
import Admin from '../views/Admin.vue'
import Home from '../views/Home.vue'
import Dashboard from '../views/Dashboard.vue'
import Documentation from '../views/Documentation.vue'
import Cart from '../views/CartPage.vue'
import Account from '../views/AccountPage.vue'
import Daystrial from '../views/Daystrial.vue'
import Product from '../views/Product.vue'
import LoginPage from '../views/LoginPage.vue'
import Register from '../views/Register.vue'
import NotFound from '../views/404.vue'
import ProductDetails from '../views/ProductDetails.vue'
import Checkout from '../views/Checkout.vue'
import Profile from '../views/Profile.vue'
import Chat from '../views/Chat.vue'

import Imprint from '../views/imprint.vue'
import FrequentlyAskedQuestions from '../views/frequently-asked-questions.vue'
import BuildYourPlugin from '../views/build-your-plugin.vue'
import DisclaimerAndLegalNotice from '../views/disclaimer-and-legal-notice.vue'



Vue.use(VueRouter)

const routes = [
  
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: Dashboard,
  },
  {
    path: '/chat',
    name: 'Chat',
    component: Chat,
  },
  {
    path: '/admin',
    name: 'Admin',
    component: Admin,
  },
  {
    path: '/logout',
    name: 'Logout',
    component: Home,
  },
  {
    path: '/404',
    component: NotFound,
    name: 'NotFound',
    
},
  {
    path: '/documentation',
    name: 'Documentation',
    component: Documentation,
    
  },
  {
    path: '/cart',
    name: 'Cart',
    component: Cart,
    
  },
  {
    path: '/login',
    name: 'Login',
    component: LoginPage,
    

  },
  {
    path: '/days-trial',
    name: 'Daystrial',
    component: Daystrial,
    
  },
  {
    path: '/our-products-page',
    name: 'Product',
    component: Product,
    
  },
  {
    path: '/my-account',
    name: 'Account',
    component: Account,
    
  },
  {
    path: '/register',
    name: 'Register',
    component: Register,
  },
  {
    path: '/product/:productId/details',
    name: 'ProductDetails',
    component: ProductDetails,
	props: true,
  },
  {
    path: '/checkout',
    name: 'Checkout,',
    component: Checkout,
   // meta: { authRequired:true }
    
  },
  {
    path: '/profile',
    name: 'Profile',
    component: Profile,
    
  },
  {
    path: '/imprint',
    name: 'Imprint',
    component: Imprint,
    
  }, 
  {
    path: '/frequently-asked-questions',
    name: 'frequentlyAskedQuestions',
    component: FrequentlyAskedQuestions,
    
  }, 
  {
    path: '/build-your-plugin',
    name: 'buildYourPlugin',
    component: BuildYourPlugin,
    
  }, 
  {
    path: '/disclaimer-and-legal-notice',
    name: 'disclaimerAndLegalNotice',
    component: DisclaimerAndLegalNotice,
    
  },
	{ path: '*',  redirect: { path: '/404' } },
	//{ path: '/:pathMatch(.*)*', name: 'NotFound', component: NotFound },
]

const router = new VueRouter({
  routes,
  base: process.env.BASE_URL,
  mode: 'history'
})
 router.beforeEach((to, from, next) => {

  const requiresAuth = to.matched.some(record => record.meta.authRequired);
  let authenticationresponse = sessionStorage.getItem('token')
  if (!authenticationresponse) {
    if (requiresAuth) {
      next({ path: '/login' });
    }else {
      next();
    } 

  }else {
    next();
  }
  if (to.path == '/logout') {
    sessionStorage.removeItem('token')
    sessionStorage.removeItem('username')
    sessionStorage.removeItem('email')
    sessionStorage.removeItem('phone')
    sessionStorage.removeItem('avatar')
    next({ path: '/' });
    window.location.reload()
  }
})


export default router

