<template>
  <v-card 
    :loading="loading"
    class="mx-auto my-12"
    max-width="90%"
  >
    <template slot="progress">
      <v-progress-linear
        color="black"
        height="10"
        indeterminate
      ></v-progress-linear>
    </template>

<div v-for="(details, dt) in product.details"
		:key="`product-details-${dt}`"
>

	<div v-for="(image, im) in details.images"
			:key="`product-detail-image-${im}`">

		<v-img
			v-bind:src="`${ image.url }`"
		></v-img>
		
	</div>
	
    
    <v-card-title><h1>{{ product.name }}</h1></v-card-title>

    <v-card-text>
      <v-row
        align="center"
        class="mx-0"
      >
        <v-rating
          :value="4.5"
          color="amber"
          dense
          half-increments
          readonly
          size="14"
        ></v-rating>

        <div class="grey--text ms-4">
          4.5 (413)
        </div>
      </v-row>

      <div class="my-4 text-subtitle-1">
         {{ product.price }} 
			<v-btn
			color="primary lighten-2"
			text
			@click="reserve"
			>
			ADD TO CART
			</v-btn>

      </div>

      
	
<!-- <v-btn class="font-weight-black rounded-0" block v-show="!product.incart" :disabled="product.name == 'Coming soon'" color="primary lighten-2" outlined @click="addToCart(product.name,product.src,product.price,1)">ADD TO CART<v-icon class="px-2">mdi-cart-outline</v-icon></v-btn>
-->
	<p>
		<b>{{ details.title }}</b>
	</p>

	

    </v-card-text>

    <v-divider class="mx-4"></v-divider>

    <v-card-title>Description</v-card-title>

    <v-card-text>


	<div v-for="(posts, dt) in details.post"
			:key="`product-details-posts-${dt}`"
	>
	
		<div v-html="`${ posts.text }`"></div>
		
		<div v-for="(image, im) in posts.images"
				:key="`product-detail-image-${im}`">

			<v-img
				v-bind:src="`${ image.url }`"
			></v-img>
			
		</div>
		
		
	</div>
 
    </v-card-text>

    <v-card-actions>
			<v-btn
			color="primary lighten-2"
			text
			@click="reserve"
			>
			ADD TO CART
			</v-btn>
    </v-card-actions>
	
</div>

	
  </v-card>
</template>

<script>
import { doc, getDoc, getFirestore } from "firebase/firestore";
import {shoppingCart} from '../views/fonction/cart.js';


export default {
	data(){
		return{
			loading: false,
			selection: 1,
			productId: this.$route.params.productId,
			product :{}
		}
	},
	async created(){
		console.log(this.$route.params.productId);
		console.log(this);
	
		const db=getFirestore();
		const snap = await getDoc(doc(db, 'Products', this.productId))

		if (snap.exists()) {
			console.log(snap.data())
			this.product = snap.data()
		}
		else {
			console.log("No such document")
		}

console.log(this.productDetails);
		
	},
	methods: {
      reserve () {
        this.loading = true

        setTimeout(() => (this.loading = false), 2000)
      }, 
		addToCart : async function(name,image,price,count){

			if(name != 'Coming soon'){
				
				shoppingCart.addItemToCart(name,image,price,count)
				this.$store.commit('inCart')
				this.$store.commit('cartCount')
				this.$store.commit('productInCard')
				location.reload();
			}
		}
    },
	computed: {           
        libText:function(str){
            // return directly html
            return str;
        }
    }

}
</script>