<template>
  <v-row align="center" justify="center" class="pa-4 checkstyle">
    <v-card
      class="pa-4 mt-4"
      elevation="3"
      outlined
    >
            <span> -> create your GaJeLabs account before the PayPal purchase</span><br>
            <span> -> The PayPal button below will turn blue(active) after our registration or conexion</span>
      <v-row class="justify-center">

        <v-col cols="12" sm="6" md="6" lg="4">
          <img src="../assets/payment.svg" alt="" max-width="100px" width="100%">
          
          <stripe v-if="false"/>
          <paypal v-if="authentication"/>

          <div v-if="!authentication" class="row justify-center ma-2">
              <v-btn
                disabled
                width="250px"
                color="blue"
                  >
                <i class='fa fa-paypal'></i>
                Paypal
              </v-btn>
          </div>
        </v-col>

        <v-col cols="12" sm="6" md="5" lg="5">

          <login :islogin="val" :register="false" v-on:register_val="getval" />
          <register :islogin="!val" :login="false" v-on:login_val="getval" />
          <detailspay />

        </v-col>

      </v-row>

    </v-card>
  </v-row>
</template>

<script>
import Login from '../components/version_1/Login.vue'
import Register from '../components/version_1/Register.vue'
import Paypal from '../components/version_1/Paypal.vue'
import Stripe from '../components/version_1/Stripe.vue'
import Detailspay from '../components/version_1/Detailspay.vue'

export default {
  components:{
   Register,
   Login,
   Paypal,
   Stripe,
   Detailspay
  },
  data(){
    return{
      val : true
    }
  },
  methods : {
    getval(){
      this.val = !this.val
    }
  },
  computed: {
    authentication: function(){
      if (sessionStorage.getItem('token') == null){
          return false
      }else{
          return true
      }
    }
  }
}
</script>
<style>
.checkstyle{
  background-color: #F9F9F9;
}
/* .paypal-button{
  pointer-events: none;
  opacity: 70%;
} */
</style>