<template>

<v-container>
    <v-container class="fill-height">
      <v-row class="fill-height pb-14" align="end">
        <v-col>
          <div v-for="(item, index) in chat" :key="index" :class="['d-flex flex-row align-center my-2', item.from == 'user' ? 'justify-end': null]">
            <span v-if="item.from == 'user'" class="primary--text mr-3">{{ item.msg }}</span>
            <v-avatar :color="item.from == 'user' ? 'indigo lighten-5': 'red lighten-5'" size="60">
                <v-avatar
                v-if="item.from == 'admin'"
                color="primary"
                class="ma-1"
                size="70"
                >      <img
                        src="../../assets/admin.png"
                        alt="John"
                    >
                </v-avatar>

              <span v-if="item.from == 'user'" class="primary--text"> Admin </span>
            </v-avatar>
            <span v-if="item.from != 'user'" class="primary--text ml-3">{{ item.msg }}</span>
          </div>
        </v-col>
      </v-row>
    </v-container>
      <v-container class="ma-0 pa-0">
        <v-row no-gutters>
          <v-col>
            <div class="d-flex flex-row align-center">
              <v-text-field v-model="msg" placeholder="Type Something" @keypress.enter="send"></v-text-field>
              <v-btn icon class="ml-4" @click="send">
                <v-icon>mdi-send</v-icon>
              </v-btn>
            </div>
          </v-col>
        </v-row>
      </v-container>
</v-container>

</template>
<script>
export default {
    data(){
    return{ 
    chat: [
    ],
    msg: null,
        }
        },

  methods: {
    send: function(){
      this.chat.push(
      {
        from: "user",
        msg: this.msg
      })
      this.msg = null
      this.addReply()
    },
    addReply(){
       this.chat.push({
        from: 'admin',
        msg: "Hmm"
      }) 
    }
  }
}
</script>