<template>
<v-container v-if="islogin">
  <v-snackbar
      v-model="reponse.snackbar"
      :color="reponse.color"
     top
    >
      {{ reponse.text }}
</v-snackbar>


            <v-card max-width="500" elevation="0">
            <div class="text-md-h5 mission mb-3 text-center text-sm-h6 text-h6 font-weight-large">
             Create your Gajelabs account
            </div>
              <v-card-text>
                <v-form ref="form" >
<!--                 <image-uploader
                  class="py-4 upload"
                  :debug="1"
                  :maxWidth="200"
                  :quality="1.0"
                  :autoRotate=true
                  outputFormat="verbose"
                  :preview=true
                  :className="['fileinput', { 'fileinput--loaded' : hasImage }]"
                  :capture="false"
                  accept="image/*"
                  doNotResize="['gif', 'svg']"
                  @input="setImage"
                  @onUpload="startImageResize"
                  @onComplete="endImageResize"
                ></image-uploader> -->
                  <v-text-field v-model="user.firstName" label="Enter your First Name" name="fistName" prepend-inner-icon="mdi-account" type="text" class="rounded-0" :rules="nameRules"  outlined></v-text-field>
                  <v-text-field v-model="user.lastName" label="Enter your Last Name (optional)" name="lastName" prepend-inner-icon="mdi-account" type="text" class="rounded-0"  outlined></v-text-field>
                  <VuePhoneNumberInput no-example valid-color="#1298FF" color="#363538" size="lg" class="pb-7" border-radius="0" label="Phone Number" v-model="user.phone"  @update="onUpdate"/>
                  <v-text-field v-model="user.email" label="Enter your email" name="email" prepend-inner-icon="mdi-email" type="email" class="rounded-0" :rules="emailRules"  outlined></v-text-field>
                  <v-text-field v-model="user.password" label="Enter your password" name="password" prepend-inner-icon="mdi-lock" type="password" class="rounded-0" :rules="passRules"  outlined></v-text-field>
                   <v-text-field v-model="user.confirmPassword" label="Confirm Password" name="confirmPassword" prepend-inner-icon="mdi-lock" type="password" class="rounded-0" :rules="confirmRules" @update:error="checkPassword" outlined></v-text-field>
                  <v-btn  :loading="loading" class="rounded-0" color="#363538" @click="register" x-large block dark>Register</v-btn>
                </v-form>
              </v-card-text>
                <v-card-actions v-if="!login" class="text--secondary">
                    <v-spacer></v-spacer>
                    if you already have an account? <v-btn class="pl-2 font-weight-black text-capitalize" style="color: #363538"  v-on:click="change" elevation="0">Login</v-btn>
                </v-card-actions>
              <v-card-actions class="ml-6 mr-6 text-center">
                <p>
By continuing, you agree to Gajelabs' terms of use<a href="#" class="pl-2" style="color: #000000">Policy</a> and <a href="#" class="pl-2" style="color: #000000">Terms of Use</a></p>
              </v-card-actions>
            </v-card>
</v-container>
      
</template>

<script>
import { getAuth, createUserWithEmailAndPassword ,updateProfile} from "firebase/auth";
//import ImageUploader from 'vue-image-upload-resize'
import VuePhoneNumberInput from 'vue-phone-number-input';
import 'vue-phone-number-input/dist/vue-phone-number-input.css';
import {collection, addDoc } from "firebase/firestore";
import { db } from "../../main.js"


export default {
    components: {
    //ImageUploader,
    VuePhoneNumberInput
  },
    props: {
  login: {
    type: Boolean,
    default: true
  },
  islogin: {
    type: Boolean,
    default: false
  }
    },
    data(){
    return{ 
      results: null,
      loading:null,
      user: {
        photoURL:'',
        nameImage:'', 
        firstName:"",
        lastName:"",
        email: "",
        password: "",
        confirmPassword:"",
        phone:"",
      },
      reponse : {
      snackbar: false,
      color:'',
      text: '',
      },
      error: "",
      valid: true,
      confirmPassword:'',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
      ],
      passRules: [
        v => !!v || 'password is required',
        v => (v && v.length >= 4) || 'The password must be at least 6 characters long',
      ],
       confirmRules: [
        v => !!v || 'password is required',
        v => (v && v.length >= 4) || 'The password must be at least 6 characters long',
        v => (this.user.password === v) || 'Password must match',
      ],
      nameRules: [
        v => !!v || 'First Name is required',
      ]
  }
},
 methods: {
    onUpdate (payload) {
        this.results = payload
        console.log(payload)
      },
    change(){
        this.$emit('login_val')
      },
   setImage: function(file){
        this.user.photoURL = file.dataUrl
    },
      validate () {
        this.$refs.form.validate()
      },
      reset () {
        this.$refs.form.reset()
      },
      resetValidation () {
        this.$refs.form.resetValidation()
      },
        checkPassword(invalid) { 
    // correct: false
    if (true == invalid) {
      this.validPassword = false;
    } else {
      this.validPassword = true;
    }
   },
async register(){
  
this.loading = true
const auth = getAuth();
await createUserWithEmailAndPassword(auth,this.user.email, this.user.password)
  .then(async(userCredential) => {

console.log(this.user.phone.formattedNumber)
    var user = userCredential.user;
    updateProfile(user, {
    displayName : this.user.firstName + ' ' + this.user.lastName,

}).then(() => {

}).catch((error) => {
  console.log(error)
});
  try{
 const docRef = await addDoc(collection(db, "Users"), {
    country : this.results.countryCode,
    email :this.user.email,
    firstname :this.user.firstName,
    lasname :this.user.lastName,
    phone : this.results.formattedNumber,
    photo :'',
  });
  console.log("Document written with ID: ", docRef.id);
} catch (e) {
  console.error("Error adding document: ", e);
}

  sessionStorage.setItem("token",user.currentUser),
  sessionStorage.setItem('username',user.displayName)
  sessionStorage.setItem('email',user.email)
  sessionStorage.setItem('phone',user.phoneNumber)
  sessionStorage.setItem('avatar',user.photoURL)
   this.reset();
   this.reponse.text = "Your account has been successfully created";
   this.reponse.color = "success"
   this.reponse.snackbar = true;
    console.log(user)
    this.$router.replace({ name: "Home" });
  })
  .catch((error) => {
    this.loading = false;
   // const errorCode = error.code;
    //const errorMessage = error.message;
    console.log(error) 
     this.reset();
   this.reponse.text = 'invalid information';
   this.reponse.color = "error"
   this.reponse.snackbar = true;
    // ..
  });
    }
    },
}
</script>

<style>
  .custom-loader {
    animation: loader 1s infinite;
    display: flex;
  }
  @-moz-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-o-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  .img-preview{
    width: 60%;
  }
  .upload{
    text-align: center;
  }
</style>