<template>
  <div class="container">
    <h1 class="mt-4 text-center">Licence Manager</h1>
    <v-form ref="form" @submit.prevent="addUsersToLicence" lazy-validation>

      <v-row cols="12">
          <v-col cols="12"> 
           <label for="user">How many new domains do you want to add?</label>
           <v-text-field v-model="numberofAddedCustomers" label="Ex: 5" prepend-inner-icon="mdi-number" class="rounded-0" outlined
              id="user"
              name="user"
              type="number"
              min="0"
              placeholder="How many new domains do you want to add?"

            ></v-text-field>
          </v-col> 

          <v-col cols="12" >
            <label for="number">Enter Business ID:</label>
            <v-text-field v-model="businessInput" label="Ex: 111" prepend-inner-icon="mdi-number" class="rounded-0" outlined
              id="business"
              name="business"
              type="number"
              placeholder="Enter Business ID:"

            ></v-text-field>  
          </v-col> 
      </v-row>


      <v-row justify="end"> 
            
        <v-btn class="rounded-2 ma-1 text-capitalize btn-ch btn btn-dark" color="primary"><v-icon class="mx-2">
         mdi-cart-check
        </v-icon>Add new domain for Licence</v-btn>

      </v-row> 

      <div>
        <button class="btn btn-dark">
          Add new domain for Licence
        </button>
      </div>

    </v-form>


    <h3 class="mt-4 text-left">Licence View</h3>
<v-container class="cart">

            <v-card >
                <v-card-text>
                  <v-row class="head-ch">
                      <v-col class="primary lighten-2 my-0 py-1 text-left white--text font-weight-black text-uppercase">Licence ID</v-col>
                      <v-col class="primary lighten-2 my-0 py-1 text-left white--text font-weight-black text-uppercase">Business ID</v-col>
                      <v-col class="primary lighten-2 my-0 py-1 text-left white--text font-weight-black text-uppercase">Licence Type</v-col> 
                      <v-col class="primary lighten-2 my-0 py-1 text-left white--text font-weight-black text-uppercase">Domains Allowed</v-col> 
                      <v-col class="primary lighten-2 my-0 py-1 text-left white--text font-weight-black text-uppercase">Domains Spent</v-col> 
                      <v-col class="primary lighten-2 my-0 py-1 text-left white--text font-weight-black text-uppercase">Activation Date</v-col>
                      <v-col class="primary lighten-2 my-0 py-1 text-left white--text font-weight-black text-uppercase">Expiration Date</v-col>
                      <v-col class="primary lighten-2 my-0 py-1 text-left white--text font-weight-black text-uppercase">Status</v-col>
                      <v-col class="primary lighten-2 my-0 py-1 text-left white--text font-weight-black text-uppercase">Total Amount</v-col>
                      <v-col class="primary lighten-2 my-0 py-1 text-left white--text font-weight-black text-uppercase"></v-col> 

                  </v-row>
                  <v-divider></v-divider>
                </v-card-text>

                <v-card-text>
                  <v-row v-for="(entry, key) in licence" :key="key">
                    <v-col class="text-left">{{ entry.licenceId }}</v-col>
                    <v-col class="text-left">{{ entry.businessId }}</v-col>
                    <v-col class="text-left">{{ entry.licenceType }}</v-col>
                    <v-col class="text-left">{{ entry.numberOfDomainsAllowed }}</v-col>
                    <v-col class="text-left">{{ entry.numberOfDomainsSpent }}</v-col>
                    <v-col class="text-left">{{ entry.activationDate }}</v-col>
                    <v-col class="text-left">{{ entry.expirationDate }}</v-col>
                    <v-col class="text-left">{{ entry.status }}</v-col>
                    <v-col class="text-left">{{ entry.totalAmount }} EUR</v-col>
                    <v-col class="text-left"><DownloadFile /></v-col>
                  </v-row>
                </v-card-text>  
 
            </v-card>

 </v-container >



    <h3 class="mt-4 text-left">Business View</h3>

        <v-col cols="8" sm="8" md="6" lg="4"> 
          <v-card >
                <v-card-text>
                  <v-row class="head-ch">
                      <v-col cols="4" scope="col" class="primary lighten-2 my-0 py-1 text-left white--text font-weight-black text-uppercase">Licence ID</v-col>
                      <v-col cols="8" scope="col" class="primary lighten-2 my-0 py-1 text-left white--text font-weight-black text-uppercase">Business ID</v-col>          
                  </v-row>
                  <v-divider></v-divider>
                </v-card-text>

                <v-card-text>
                  <v-row v-for="(entry, key) in business" :key="key">
                    <v-col cols="4" scope="col" class="text-left">{{ entry.businessId }}</v-col>
                    <v-col cols="8" scope="col" class="text-left">{{ entry.businessName }}</v-col>
                  </v-row>
                </v-card-text>  
 
          </v-card>
        </v-col> 


  </div>

</template>


<script>
import * as moment from "moment/moment";
import DownloadFile from './DownloadFile.vue';

const licTypes = Object.freeze({
  TIME_BASED: "TIME BASED",
  NUMBER_BASED: "NUMBER BASED",
});
const licStatuses = Object.freeze({
  ACTIVE: "ACITVE",
  EXPIRED: "EXPIRED",
  OVERDRAFT: "OVERDRAFT",
  INACTIVE: "INACTIVE",
});

export default {
  name: "LicenceManager",
  components: {
        DownloadFile
  },
  data() {
    return {
      numberofAddedCustomers: null,
      businessInput: null,
      headers: [
          { text: 'items', value: 'name' },
          { text: 'price', value: 'price' },
        ],
      licence: [
        {
          licenceId: 548559,
          licenceType: licTypes.TIME_BASED,
          numberOfDomainsAllowed: 10,
          numberOfDomainsSpent: 0,
          activationDate: "",
          expirationDate: "",
          totalAmount: 0,
          status: licStatuses.INACTIVE,
          businessId: 9445,
        },
        {
          licenceId: 452115,
          licenceType: licTypes.NUMBER_BASED,
          numberOfDomainsAllowed: 10,
          numberOfDomainsSpent: 0,
          activationDate: "/",
          expirationDate: "/",
          totalAmount: 0,
          status: licStatuses.INACTIVE,
          businessId: 8475,
        },
      ],

      business: [
        {
          businessId: 9445,
          businessName: "ADORNO",
        },
        {
          businessId: 8475,
          businessName: "HATPORIUM",
        },
      ],
    };
  },

  methods: {
    addUsersToLicence() {
      // finds selected licence which is in coherence with business id provided via input on form.
      let selectedLicence = this.licence.find((d) => {
        return d.businessId == this.businessInput;
      });

      // validates input
      if (selectedLicence == null) {
        alert("Enter correct business ID");
        return;
      }

      // checks if selected licence is time based and inactive, and sets dates for first customer.
      if (
        selectedLicence.licenceType == licTypes.TIME_BASED &&
        selectedLicence.status == licStatuses.INACTIVE
      ) {
        selectedLicence.activationDate = moment().format(
          "MMMM Do YYYY, h:mm:ss a"
        );
        selectedLicence.expirationDate = moment()
          .add(1, "Y")
          .format("MMMM Do YYYY, h:mm:ss a");
      }

      for (let i = 0; i < this.numberofAddedCustomers; i++) {
        if (selectedLicence.status == licStatuses.INACTIVE) {
          selectedLicence.status = licStatuses.ACTIVE;
        }

        selectedLicence.numberOfDomainsAllowed -= 1;
        selectedLicence.numberOfDomainsSpent += 1;

        // checks if amount is greater than added users
        if (selectedLicence.numberOfDomainsAllowed < 0) {
          selectedLicence.status = licStatuses.OVERDRAFT;
          selectedLicence.totalAmount += 2;
          // checks if Licence is expired in TIME_BASED Licence type
        } else if (
          selectedLicence.licenceType == licTypes.TIME_BASED &&
          moment().format("MMMM Do YYYY, h:mm:ss a") >
            selectedLicence.expirationDate
        ) {
          selectedLicence.status = licStatuses.EXPIRED;
          selectedLicence.totalAmount += 2;
        } else {
          selectedLicence.totalAmount += 1;
        }
      }
    },
  },
};
</script>
