<template>

            <v-card max-width="500" v-if="islogin" elevation="0">
            <div class="text-md-h5 mission mb-3 text-center text-sm-h6 text-h6 font-weight-large">
             Login
            </div>
              <v-card-text>
                <v-form ref="form" v-model="valid"  lazy-validation>
                  <v-text-field v-model="email" label="Enter your email" name="email" prepend-inner-icon="mdi-email" type="email" class="rounded-0" :rules="emailRules" outlined></v-text-field>
                  <v-text-field v-model="password" label="Enter your password" name="password" prepend-inner-icon="mdi-lock" type="password" :rules="passRules" suffix="| Forgot?" class="rounded-0" outlined></v-text-field>
                  <v-btn  :loading="loading" 
                        class="rounded-0" color="#363538" @click="login" x-large block dark>Login</v-btn>
                  <v-card-actions v-if="register" class="text--secondary">
                    <v-checkbox color="#000000" label="Remember me"></v-checkbox>
                    <v-spacer></v-spacer>
                    No account? <router-link class="pl-2" style="color: #363538" v-bind:to="{ name: 'Register' }" replace>Sign Up</router-link>
                  </v-card-actions>
                  <v-card-actions v-if="!register" class="text--secondary">
                    <v-checkbox color="#000000" label="Remember me"></v-checkbox>
                    <v-spacer></v-spacer>
                    No account? <v-btn class="pl-2 font-weight-black text-capitalize" style="color: #363538"  v-on:click="change" elevation="0">Sign Up</v-btn>
                  </v-card-actions>
                </v-form>
              </v-card-text>
              <v-card-actions class="ml-6 mr-6 text-center">
                <p>
By continuing, you agree to Gajelabs' terms of use<a href="#" class="pl-2" style="color: #000000">Policy</a> and <a href="#" class="pl-2" style="color: #000000">Terms of Use</a></p>
              </v-card-actions>
            </v-card>
 
</template>

<script>
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
export default {
  props: {
      register: {
    type: Boolean,
    default: true
  },
  islogin: {
    type: Boolean,
    default: true
  }
    },
  data(){
    return{
      loading: false,
      valid: true,
      password: '',
       email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
      ],
      passRules: [
        v => !!v || 'password is required',
        v => (v && v.length >= 4) || 'The password must be at least 6 characters long',
      ],
  }
  },
      methods: {
      change(){
        this.$emit('register_val')
      },
      validate () {
        this.$refs.form.validate()
      },
      reset () {
        this.$refs.form.reset()
      },
      resetValidation () {
        this.$refs.form.resetValidation()
      },
      login(){
        this.loading = true
        const auth = getAuth();
        signInWithEmailAndPassword(auth, this.email, this.password)
          .then((userCredential) => {
            // Signed in 
            const user = userCredential.user;
              sessionStorage.setItem("token",user.currentUser),
              sessionStorage.setItem('username',user.displayName)
              sessionStorage.setItem('email',user.email)
              sessionStorage.setItem('phone',user.phoneNumber)
             // sessionStorage.setItem('avatar',user.photoURL)
            this.$router.replace("/")
            this.$store.commit('connected')
            console.log(user)
            
            // ...
          })
          .catch((error) => {
            //const errorCode = error.code;
            const errorMessage = error.message;
            console.log(errorMessage)
            this.loading =false
          
  });
      }
    },
}

</script>

<style>
  .custom-loader {
    animation: loader 1s infinite;
    display: flex;
  }
  @-moz-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-o-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
</style>