<template>
<v-container class="cart">

        <h2 v-if="!this.$store.state.totalCount"
            class="display-1 pa-16 text-center text-uppercase font-weight-medium primary--text text-capitalize ma-0"
            >
            Your cart is empty! 
            <h2 
            class="title text-center font-weight-light primary--text text-capitalize ma-0"
            >
            Already have an account? <router-link class="pl-2" style="color: #363538" v-bind:to="{ name: 'Login' }" replace>Login</router-link> to relaunch your abandoned cart
            </h2>
            </h2>




            <v-card v-if="this.$store.state.totalCount">
                <v-card-text>
            <v-row class="head-ch">
                <v-col md="8" class="primary lighten-2 my-0  py-1 text-center white--text font-weight-black text-uppercase">Product</v-col>
                <v-col cols="4" class="primary lighten-2 my-0 py-1 text-center white--text font-weight-black text-uppercase">Price</v-col>          
            </v-row>
             <v-divider></v-divider>
            </v-card-text>
            <v-card-text>
            <v-row :key="idx" v-for="(pro, idx) in this.$store.state.listCart" >
                <v-col md="8" class="text-left">
                    <v-row>
                    <v-col cols="4">
                      <v-row> 
                          <v-spacer></v-spacer> 
                    <img class="ma-1 cart_img" :src="pro.image" alt="">
                    <v-spacer></v-spacer>
                      </v-row>  
                    </v-col>
                    <v-col cols="8" class="head-ch font-weight-black">
                        {{pro.name}}
                    </v-col>
                    </v-row>
                </v-col>
                <v-col cols="4" class="text-center font-weight-black">{{pro.price}}</v-col>
        
            </v-row>
             
            </v-card-text>  
             <v-card-text> 
            <v-row> 
            <v-spacer></v-spacer> 
           <v-col cols="6" sm="3" md="2" class="head-ch py-1 text-center text-subtitle font-weight-black white--text primary lighten-2">
               TOTAL :
           </v-col>
            <v-col cols="6" sm="4" md="4" class="text-center py-1 font-weight-black white--text primary lighten-2"> 
                 {{this.totalprice}}
           </v-col>
            </v-row> 
             </v-card-text>  
            </v-card>
 <v-container class="fill-height my-2" fluid>
            <v-row> 
            <v-spacer></v-spacer> 
        <v-col cols="6" sm="4" md="3">
          </v-col>
            </v-row> 
                 <v-row justify="end"> 
            
                
                <v-btn to="/" class="rounded-2 ma-1 text-capitalize btn-ch" color="secondary" outlined>Continue shopping</v-btn>
                <v-btn elevation="0" class="rounded-2 ma-1 text-capitalize btn-ch"  @click="delete_all" color="error" outlined><v-icon>mdi-delete-outline</v-icon>Delete</v-btn>
                <v-btn to="/checkout" :disabled="this.cartdisable" class="rounded-2 ma-1 text-capitalize btn-ch" color="secondary"><v-icon class="mx-2">
       mdi-cart-check
      </v-icon>Checkout</v-btn>
            </v-row> 
</v-container> 

</v-container>        
</template>
<script>
import { shoppingCart } from './fonction/cart';
export default {
components:{

},
    data () {
      return {
          quantity: 0,
      }

    },
    computed: {
        totalprice :function() {
             var total_price = this.$store.state.listCart.reduce((accumulator, current) => accumulator + parseFloat(current.total), 0);
             sessionStorage.setItem('total_price',total_price)
             return   total_price
        },
        cartdisable : function(){
            if(this.$store.state.totalCount >= 1 ){
                return false
            }
            else{
                return true
            }
        }
    },
    methods:{
        delete_all:function(){
            shoppingCart.clearCart()
            this.$store.commit('cartList')
            this.$store.commit('cartCount')
        },
        minus: function(name, count){
        if(count > 1)
        {
       shoppingCart.setCountForItem(name,Number(count) - 1)
       this.$store.commit('cartList')
       }
        },
        plus: function(name, count){
            
             shoppingCart.setCountForItem(name,Number(count) + 1)
             this.$store.commit('cartList')
        }
    },
    mounted() {
        this.$store.commit('cartList')
    },
}
</script>
<style scoped>
.cart_img{
    max-height: 45px;
}
.cart_img:hover{
    transform: scale(2);
}
.cart{
    font-family: font2;
}
.btn-ch{
    font-family: font1;
}
.head-ch{
    font-family: font1;
}
</style>