<template>
  <v-app-bar class="elevation-0" color="white" fixed flat fluid app> 
    <v-container class="pa-0" fluid>
      <v-toolbar  flat>
        <!-- Mobile header -->
        <div class="hidden-sm-and-up" v-if="false">
          <v-toolbar-items >
            <v-menu
              rounded="0"
              transition="slide-x-transition"
              v-on="on"
              content-class="gaje-top gaje-boder-top gaje-mobile-menu-content elevation-0 rounded-0"
              min-width="100vw"
              offset-y
            >
              <template v-slot:activator="{ on, attrs }">
                <v-card-text v-bind="attrs" v-on="on"
                  ><v-icon>mdi-text</v-icon></v-card-text>
              </template>
              <v-card class="gaje-roundered-0">
                <v-menu
                  rounded="0"
                  open-on-hover
                  open-on-focus
                  :value="menu.value"
                  v-on="on"
                  offset-y
                  content-class="gaje-top gaje-boder-top elevation-0"
                  min-width="100vw"
                  v-for="(menu, index1) in menus"
                  :key="index1"
                  height="100%"
                  eager
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-card-actions 
                      elevation=0
                      v-bind="attrs" 
                      v-on="on"
                      rounded="0"
                      class="pa-3"
                    >
                      <nobr class="font-weight-bold">{{menu.title}}<v-icon>mdi-chevron-down</v-icon></nobr>
                    </v-card-actions>
                  </template>
                  <v-card 
                      elevation=0
                      v-bind="attrs" 
                      v-on="on"
                      class="pa-4 gaje-roundered-0 rounded-0"
                      rounded="0"
                    >
                    <v-list>
                      <v-list-item
                        v-for="(link, index3) in menu.submenus"
                        :key="index3"
                        class="pa-0 ma-0 gaje-menu-list"
                      >
                        <router-link class="text-uppercase text-decoration-none text-capitalize" :to="link.url">
                          {{ link.title }}
                        </router-link>
                      </v-list-item>
                    </v-list> 
                  </v-card>
                </v-menu>
              </v-card>
            </v-menu>
          </v-toolbar-items>
        </div>
        <!-- end header mobile -->
        <v-toolbar-title class="pa-0">
          <a href="/">
            <img class="hidden-sm-and-down" src="../../assets/logo.png" height="50"/>
            <img class="d-none d-sm-flex d-md-none" src="../../assets/logo.png" height="35"/>
            <img class="d-sm-none hidden-sm-and-up" src="../../assets/logo.png" height="40"/>
          </a>
        </v-toolbar-title>


        <div class="d-none d-sm-flex" v-if="false" >
          <v-toolbar-items 
          >
            <v-menu
              open-on-hover
              open-on-focus
             
              :value="menu.value"

              v-on="on"
              offset-y
              :content-class="'gaje-menu-content elevation-0 rounded-0 gaje-header-content-'+index1"

              min-width="100vw"
              v-for="(menu, index1) in menus"
              :key="index1"
              height="100%"
              eager
              

            >
              <template v-slot:activator="{ on, attrs }">
                <v-card 
                  elevation=0
                  v-bind="attrs" 
                  v-on="on"
                  content-class="gaje-menu-title"
                  
                  :class="'pa-3 gaje-header-title title-'+index1"

                >
                  <nobr class="font-weight-bold">{{menu.title}}<v-icon>mdi-chevron-down</v-icon></nobr>
                </v-card>
              </template>
              <v-card 
                  elevation=0
                  v-bind="attrs" 
                  v-on="on"
                  class="pa-0 gaje-menu-title gaje-roundered-0"

                >
                <v-container
                  class="gaje-roundered-0"
                >
                  <v-row 
                  >
                    <v-col>
                      
                      <v-card
                        elevation=0
                        
                        v-for="(item, index2) in menu.submenus"
                        :key="index2"
                        width="33%"
                        max-width="33%"
                        
                        max-height="25vh"
                        min-height="20vh"
                        class="ma-0 "
                        style="display:inline-block;align-self: top;"
                      >
                        
                        <v-list>
                          <v-list-item-title
                          class="gaje-menu-list-title text-uppercase"
                          ><b>{{ item.title }}</b>
                          </v-list-item-title>
                          <v-list-item
                            v-for="(link, index3) in item.submenus"
                            :key="index3"
                            class="pa-0 ma-0 gaje-menu-list"
                          >
                            <router-link class="font-weight-medium text-decoration-none text-capitalize" :to="link.url">
                              {{ link.title }}
                            </router-link>
                          </v-list-item>
                        </v-list> 
                      </v-card>
                    </v-col>
                    <v-col
                     class="d-none d-md-flex ma-0"
                    >
                     
    <v-row v-if="menu.title == 'Products'" class="my-0">
        <v-col md="6" sm="6" cols="12"   :key="pro" v-for="pro in products">
                <v-card
                  class="mx-auto elevation-3"
                  color="white"
                  min-width="200"
                  max-width="320"
                  v-if="pro.id <= 2"
                >
                <v-row > 
                  <v-col class="pr-0 pt-0 ma-0" cols="8">
                  <v-img
                    class="img_pd white--text align-end mt-4 px-0"
                    height="70%"
                    :src="pro.src"
                  >
                  </v-img>
                  </v-col>
                  <v-col class="pl-0 pt-0 justify-center ma-0" cols="4">
                  <v-list  color="white" class="px-2 mt-4">
          <v-list-item>
            <v-tooltip class="pa-0"  color="white" left>
            <template v-slot:activator="{ on, attrs }">
             <v-btn  v-bind="attrs" v-on="on" color="white" elevation="0" x-small fab><v-icon color="primary lighten-3">mdi-cards-heart-outline</v-icon></v-btn>
              </template>
              <span class="primary--text text-right">like</span>
            </v-tooltip>
           </v-list-item> 
          <v-list-item>
            <v-tooltip color="white"  left>
            <template v-slot:activator="{ on, attrs }">
            <v-btn v-show="!pro.incart" :disabled="pro.name == 'Coming soon'" v-bind="attrs" v-on="on" color="white" elevation="0"  @click="addToCart(pro.name,pro.src,pro.price,1)" x-small fab><v-icon color="primary lighten-3">mdi-cart-outline</v-icon></v-btn>
            </template>
              <span class="primary--text">Add to cart</span>
            </v-tooltip>
            <v-tooltip color="white"  left>
            <template v-slot:activator="{ on, attrs }">
        <v-btn to="/cart" v-show="pro.incart" v-bind="attrs" v-on="on" color="white" elevation="0" x-small fab><v-icon color="primary lighten-3">mdi-check</v-icon></v-btn>
            </template>
              <span class="primary--text">view cart</span>
            </v-tooltip>
         </v-list-item>
            <v-list-item>
            <v-tooltip color="white"  left>
            <template v-slot:activator="{ on, attrs }">
             <v-btn v-bind="attrs" v-on="on" color="white" elevation="0" x-small fab><v-icon color="primary lighten-3">mdi-eye</v-icon></v-btn>
            </template>
            <v-container>
               <span class="primary--text">View details</span>
            </v-container>
             
            </v-tooltip>
           </v-list-item>
            <v-list-item>
            <v-tooltip color="white"  left>
            <template v-slot:activator="{ on, attrs }">
             <v-btn :disabled="pro.name == 'Coming soon'" href="https://firebasestorage.googleapis.com/v0/b/gajelabs-vue.appspot.com/o/gajeplugin.zip?alt=media&token=05a1e36e-882b-4706-ac79-49d24d60abd3" v-bind="attrs" v-on="on" color="white" elevation="0" x-small fab><v-icon color="primary lighten-3">mdi-download</v-icon></v-btn>
            </template>
              <span class="primary--text">Download file</span>
            </v-tooltip>           
           </v-list-item>
                  </v-list>
                  </v-col>
                </v-row>
                <v-card-text class="text--primary py-1">
                    <div><h3 class="text-center primary--text text-uppercase">{{pro.name}}</h3></div>
                  </v-card-text>
                  <v-card-text class="text--primary py-1">
                    <div class="description"><h3 class="text-justify font-weight-light">{{pro.description}}</h3></div>
                  </v-card-text>
                <v-card-text class="text--primary py-1">
                <div><h2 class="secondary--text text-center font-weight-block"> {{pro.price}}</h2></div>
                </v-card-text>
                </v-card>
            </v-col>
    </v-row>



                    </v-col>
                  </v-row>
                </v-container>
              </v-card>
            </v-menu>
          </v-toolbar-items>
          <v-toolbar-items>
            <v-menu

              v-on="on"
              offset-y
              content-class="elevation-0 rounded-0"

            >
              <template v-slot:activator="{ on, attrs }">
                <v-card 
                  elevation=0
                  v-bind="attrs" 
                  v-on="on"
                  class="ma-3 gaje-header-title-dots"
                  style="display: none;"
                >
                  <v-icon>mdi-dots-vertical</v-icon>
                </v-card>
              </template>
              <v-card 
                  elevation=0
                  v-bind="attrs" 
                  v-on="on"
                  class="mx-auto"
                  min-width="150"
                  tile
              >
                <v-list dense fluid>
                 
                  <v-list-item
                    v-for="(menu, index) in menus"
                    :key="index"
                    style="display: none;"
                    :class="'title-bis-'+index"
                    @click="displayMenu(index,menus)"
                  
                  >
                    <v-list-item-content >
                      <v-list-item-title><nobr>{{menu.title}}<v-icon>mdi-chevron-down</v-icon></nobr></v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                 
                </v-list>
              </v-card>
            </v-menu>
          </v-toolbar-items>
        </div>

        <v-spacer></v-spacer>
        <v-toolbar-items >

        <router-link 
          
          class="font-weight-bold text-decoration-none pa-4" to="/cart">
          <v-badge :value="this.$store.state.totalCount" color="secondary" overlap :content="this.$store.state.totalCount">
            <v-tooltip bottom color="primary">
              <template v-slot:activator="{ on, attrs }">
                <v-icon  v-bind="attrs"
          v-on="on">mdi-cart-outline</v-icon>
              </template>
              <span >Cart</span>
            </v-tooltip>
          </v-badge> 
        </router-link>
           
            
       
        </v-toolbar-items>
        <div class="d-none d-sm-flex">
          <v-toolbar-items>
            <v-divider
              vertical
              inset
              class="gaje-divider"
              
            ></v-divider>

              <router-link v-if="!this.$store.state.login" class="gaje-sign-in font-weight-bold text-decoration-none px-5 py-4" to="/dashboard">
                <nobr>Dashboard</nobr>
              </router-link>
            
              <router-link v-if="this.$store.state.login" class="gaje-sign-in font-weight-bold text-decoration-none px-5 py-4" to="/login">
                <nobr>Sign in</nobr>
              </router-link>
              <router-link v-if="!this.$store.state.login" class="gaje-sign-in font-weight-bold text-decoration-none px-5 py-4" to="/logout">
                <nobr>Log out</nobr>
              </router-link>

            <router-link v-if="this.$store.state.login" class="gaje-sign-up font-weight-bold text-decoration-none px-5 py-3" to="/register">
              
              <v-btn
                rounded
                color="primary"
                dark
              >
                Sign up
              </v-btn>
          
            </router-link>
          </v-toolbar-items>

      </div>
      </v-toolbar>
    </v-container>
  </v-app-bar> 
</template>

<script>
import {shoppingCart} from '../../views/fonction/cart.js';
export default {
  name: "Header",
  methods:{
        addToCart: function(name,image,price,count){
    if(name != 'Coming soon'){
    shoppingCart.addItemToCart(name,image,price,count)
    
    this.$store.commit('inCart')
    this.$store.commit('cartCount')
    this.$store.commit('productInCard')

    }

  },

  },   
  computed: {
  products() {
      return this.$store.state.products.dokans
  },
  },
  
  mounted: function() {
     this.$store.commit('productInCard')
        this.$store.commit('inCart')
        var menus = this.menus;

        document.addEventListener('click',function () {
          setTimeout(function(){
            
            if (localStorage.getItem('contextMenuOpen') == "true") {
              localStorage.setItem('contextMenuOpen',"false");
            }else{
              let index = localStorage.getItem('menuIndex');
              menus[index].value = false;
            }
          },100);
        });
    },
  data: () => ({
    displayMenu: function(index,menus){
        document.getElementsByClassName('gaje-header-content-'+index)[0].style.top ="56px";
        document.getElementsByClassName('gaje-header-content-'+index)[0].style.position ="fixed";
        for (var i = menus.length - 1; i >= 0; i--) {
          if (i==index) {
            menus[index].value = !menus[index].value;
          }else{
            menus[i].value = false;
          }
          
        }
        

        localStorage.setItem('contextMenuOpen',"true");
        localStorage.setItem('menuIndex',index);

        
          //console.log(document.getElementsByClassName('title-'+index)[0]);
      },
    selectedItem: 1,   
    on:true,
    alignments: [
        'start',
        'center',
        'end',
      ],
    true:true,
    menuState:{
      offset_y:true,
    },
    items: [
        { title: 'Click Me' },
        { title: 'Click Me' },
        { title: 'Click Me' },
        { title: 'Click Me 2' },
      ],
    menus:[
        {
            title:"Products",
            url: "/products",
            slug:"products",
            value:false,
            submenus:[
                {
                    title:"Accounting",
                    url:"/products/accounting",
                    slug:"accounting",
                    submenus:[
                        {
                            title: "WCFM",
                            url:"/products/accounting/wcfm",
                            slug:"wcfm"
                        },
                        {
                            title: "Dokan",
                            url:"/products/accounting/dokan",
                            slug:"dokan"
                        },
                        {
                            title: "WC Vendor",
                            url:"/products/accounting/wc-vendor",
                            slug:"wc-vendor"
                        },
                        {
                            title: "WCFM",
                            url:"/products/accounting/wcfm",
                            slug:"wcfm"
                        }
                    ]
                },
                {
                    title:"Tax ",
                    url:"/products/tax-calculation",
                    slug:"tax-calculation",
                    submenus:[
                        {
                            title: "Global Tax ",
                            url:"/products/tax-calculation/gtc",
                            slug:"gtc"
                        },
                        {
                            title: "Global Tax ",
                            url:"/products/tax-calculation/gtc",
                            slug:"gtc"
                        },
                        {
                            title: "Global Tax ",
                            url:"/products/tax-calculation/gtc",
                            slug:"gtc"
                        },
                        {
                            title: "Global Tax ",
                            url:"/products/tax-calculation/gtc",
                            slug:"gtc"
                        }
                    ]
                },
                {
                    title:"Shipping",
                    url:"/products/shipping",
                    slug:"shipping",
                    submenus:[
                        {
                            title: "WCFM",
                            url:"/products/accounting/wcfm",
                            slug:"wcfm"
                        },
                        {
                            title: "Dokan",
                            url:"/products/accounting/dokan",
                            slug:"dokan"
                        },
                        {
                            title: "WC Vendor",
                            url:"/products/accounting/wc-vendor",
                            slug:"wc-vendor"
                        },
                        {
                            title: "WCFM",
                            url:"/products/accounting/wcfm",
                            slug:"wcfm"
                        }
                    ]
                },
                {
                    title:"Payment",
                    url:"/products/payment",
                    slug:"payment",
                    submenus:[
                        {
                            title: "Manage  gateway",
                            url:"/products/payment/mpg",
                            slug:"mpg",
                        },
                        {
                            title:"PayPal  payment",
                            url:"/products/payment/paypal-qr-code",
                            slug:"paypal-qr-code"

                        },
                        {
                            title: "Dokan Splite Payment",
                            url:"/products/payment/dokan-split-paypal",
                            slug:"dokan-split-paypal",
                        },
                        {
                            title: "WC  Splite Payment",
                            url:"/products/payment/wc-vendor-split-paypal",
                            slug:"wc-vendor-split-paypal",
                        }

                    ]
                },
                {
                    title:"Currencies handling",
                    url:"/products/currencies-handling",
                    slug:"currencies-handling",
                    submenus:[
                        {
                            title: "Multicurrencies ",
                            url:"/products/currencies-handling/multicurrencies-marketplace",
                            slug:"multicurrencies-marketplace",
                        },
                        {
                            title: "Woocommerce ",
                            url:"/products/currencies-handling/woo-booking",
                            slug:"woo-booking",
                        },
                        {
                            title: " products addons",
                            url:"/products/currencies-handling/woo-product",
                            slug:"woo-product",
                        },
                        {
                            title: " auction products",
                            url:"/products/currencies-handling/woo-auction",
                            slug:"woo-auction",
                        }
                    ]
                },
                {
                    title:"AWS migration",
                    url:"products/aws-it-service",
                    slug:"aws-it-service",
                    submenus:[
                        {
                            title: "Manage  gateway",
                            url:"/products/payment/mpg",
                            slug:"mpg",
                        },
                        {
                            title:"PayPal  payment",
                            url:"/products/payment/paypal-qr-code",
                            slug:"paypal-qr-code"

                        },
                        {
                            title: "Dokan Splite Payment",
                            url:"/products/payment/dokan-split-paypal",
                            slug:"dokan-split-paypal",
                        },
                        {
                            title: "WC  Splite Payment",
                            url:"/products/payment/wc-vendor-split-paypal",
                            slug:"wc-vendor-split-paypal",
                        }
                    ]
                }
            ]
        },
        {
          title:"Documentation",
          url:"/documentations",
          value:false,
          submenus:[
          ]
        },
        {
          title:"Compatibilitie",
          url:"/documentations",
          value:false,
          submenus:[
          ]
        }
    ]
  }),

};
</script>
<style scoped>
  .gaje-top{
    position: fixed !important;
    top: 30px !important;
  }
  .gaje-boder-top{
    border-top: 1px #ccc solid !important;
  }
  .gaje-roundered-0{
    border-radius: 0px !important;
  }
  .gaje-menu-content{
    border-top: 1px #ccc solid;
    border-bottom: 1px #ccc solid;
    
  }
  .gaje-menu-title{
    min-width: 50px !important;
    margin-left: 0px !important;
    margin-right: 0px !important;
    padding: 0px!important;
  }
  .gaje-mobile-menu-content{
    border-radius: 0px !important;
    border: 1px #ccc solid;
    z-index: 10000;
    
  }
  .gaje-menu-list{
    min-height: 20px !important;
    height: 25px !important;
  }
  .gaje-menu-list-title{
    margin-bottom: 20px !important;
    
  }
  .gaje-divider{
    height: 50% !important;
    margin-top: 15px !important;

  }
  .gaje-sign-in:hover{
    text-decoration: underline !important;
  }
  .gaje-sign-up>div>button{
    padding-right: 20px !important;
    padding-left: 20px !important;


  }

  .gaje-menu-list>a:hover{
    text-decoration: underline !important;
  }

  
  @media only screen and (max-width: 740px) {
    .title-1{
      display: none !important;

    }
    .title-bis-1{
      display: block !important;
    }
    .gaje-header-title-dots{
      display: block !important;
    }
  }
  @media only screen and (max-width: 885px) {
    .title-2{
      display: none !important;
    }
    .title-bis-2{
      display: block !important;
    }
    .gaje-header-title-dots{
      display: block !important;
    }
  }
  @media only screen and (max-width: 1030px) {
    .title-3{
      display: none !important;
    }
    .title-bis-3{
      display: block !important;
    }
    .gaje-header-title-dots{
      /*display: block !important;*/
    }
  }
  @media only screen and (max-width: 1030px) {
    .title-4{
      display: none !important;
    }
    .title-bis-4{
      display: block !important;
    }
    .gaje-header-title-dots{
      /*display: block !important;*/
    }
  }
</style>
