   <span class="primary--text">This plugin is compatible to: </span>
</v-container>


<!-- PLATEFORMS -->
              <v-list
                v-for="(compatibility, cp) in pro.compatibilities"
                  :key="`compatibility-plateform-${cp}`"
              >

