				</v-card-text> 


                <v-card-text class="text-center pt-0 pb-2">
                <v-btn class="font-weight-black rounded-0" block v-show="!pro.incart" :disabled="pro.name == 'Coming soon'" color="primary lighten-2" outlined @click="addToCart(pro.name,pro.src,pro.price,1)">ADD TO CART<v-icon class="px-2">mdi-cart-outline</v-icon></v-btn>
                <v-btn class="font-weight-large rounded-0" block v-show="pro.incart" :disabled="pro.name == 'Coming soon'" color="primary lighten-2" outlined to="/cart">VIEW CART<v-icon class="px-2">mdi-check</v-icon></v-btn>
