 </v-container>       



  




</template>
<script>
import {shoppingCart} from '../../views/fonction/cart.js';
    export default {
        data: () => ({
            }),
             methods :{
          addToCart:async function(name,image,price,count){
            if(name != 'Coming soon'){
            shoppingCart.addItemToCart(name,image,price,count)
            this.$store.commit('inCart')
            this.$store.commit('cartCount')
            this.$store.commit('productInCard')
            location.reload();
            }

          },
            },
    mounted() {
        this.$store.commit('productInCard')
        this.$store.commit('inCart')
    },
        computed: {
        products() {
            return this.$store.state.products
        },
        }
  }
</script>
