  mutations: {
    cartCount(state){
      state.totalCount = shoppingCart.totalCount()
    },
    cartList(state){
      state.listCart = shoppingCart.listCart()
    },
    productInCard(state){
      state.productInCard = [];
      state.listCart.forEach(function(obj = state.listCart){
        state.productInCard.push(obj.name);
      })
    },
    inCart: function(state){
      var incart = state.productInCard;
      state.products.forEach(function(valeur,cle) {
      console.log(cle.d);
      valeur['incart'] = incart.includes(valeur.name)
  
      }, state.products);
    },
    connected: function(state){
      let authenticationresponse = sessionStorage.getItem('token')
      if(authenticationresponse){
        state.login = false
      }else{
        state.login = true
      }
    } 
  },
